.search {
  grid-column: 2;
  grid-row: 2;
  position: relative;
  min-width: 400px;
  height: 40px;
  margin-bottom: 1rem;
}

.submit {
  position: absolute;
  right: 70px;
  z-index: 4;
}

@media only screen and (max-width: 600px) {
  .search {
    grid-column: 1/span 3;
  }

  input[type=text] {
    width: 100%;
    border-radius: 0;
  }

  button {
    border-radius: 0;
  }
}



