.monitor {
  background: white;
  border-radius: 3px;
  -webkit-box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.6);
          box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.6);
  margin-top: 0;
  opacity: 1;
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  padding: 10px;
  margin-bottom: 10px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto auto 1fr;
}
.monitor.hidden {
  margin-top: 40%;
  opacity: 0;
}
.monitor img {
  grid-row: 1/span 3;
  width: 60px;
}
.monitor .title {
  grid-row: 1;
  padding: 0 0.2em 0.2em 0.5em;
}
.monitor .time {
  grid-row: 2;
  padding: 0.2em 0.2em 0.2em 0.5em;
}
.monitor .fa-times {
  color: rgba(50, 50, 50, 0.5);
  cursor: pointer;
  opacity: 0;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}
.monitor .fa-times.visible {
  opacity: 1;
}

@media only screen and (max-width: 600px) {
  .monitor {
    margin-left: 1rem;
    margin-right: 1rem;
  }
}



