.region {
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  height: 40px;
  border-radius: 0 3px 3px 0;
  border: 0;
  border-left: 1px solid #a0a0a0;
  background: #e0e0e0;
  width: 60px;
  z-index: 3;
}

.countries {
  height: 0;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  overflow: hidden;
  width: 360px;
  margin: auto;
  -webkit-box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
          box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
  padding: 0 10px 0 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  background: white;
  border-radius: 0 0 5px 5px;
  z-index: 1;
  text-align: center;
}
.countries.expanded {
  height: 120px;
  padding: 50px 10px 10px;
}

.country {
  height: 40px;
  line-height: 40px;
  cursor: pointer;
}
.country:hover {
  background: rgba(241, 217, 165, 0.8);
}
.country.current {
  background: rgba(241, 217, 165, 0.5);
}



