
body, html {
    margin: 0;
    padding: 0;
    font-family: sans-serif;

    background: -webkit-radial-gradient(100% 300%, circle, #f1d9a5, #16333b);

    background: -o-radial-gradient(100% 300%, circle, #f1d9a5, #16333b);

    background: radial-gradient(circle at 100% 300%, #f1d9a5, #16333b);
    height: 100%;
    min-height: 100%;
    overflow-y: scroll;
}

.root {
    min-height: 100%;
    height: 100%;

}


